@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2");
  font-display: swap;
}
*,
::after,
::before {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: #f2f2f2;
}

body {
  width: 100%;
  min-width: 320px;
  max-width: 1200px;
  min-height: 100%;
  margin: 0 auto;
  padding: 0;
  font-style: normal;
  font-size: 14px;
  line-height: 21px;
  font-family: "PT Sans", "Arial", sans-serif;
  background-color: #ffffff;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
}

img {
  max-width: 100%;
  object-fit: cover;
  object-position: center;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.header {
  position: relative;
  min-height: 56px;
}

.header__logo {
  position: absolute;
  top: 0;
  left: 50%;
  z-index: 1;
  display: block;
  width: 185px;
  height: 96px;
  transform: translateX(-50%);
}

.main-navigation__toggle {
  position: absolute;
  top: 17px;
  right: 20px;
  z-index: 10;
  display: none;
  width: 23px;
  height: 22px;
  background-color: transparent;
  background-image: url("../img/icon-menu.svg");
  background-repeat: no-repeat;
  background-size: contain;
  border: none;
}
@media (min-width: 768px) {
  .main-navigation__toggle {
    display: none;
  }
}

.main-navigation__container {
  position: relative;
  top: 0;
  right: 0;
  left: 0;
  z-index: 5;
  display: block;
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .main-navigation__container {
    position: static;
    display: grid;
    justify-content: space-between;
    background-color: #ffffff;
    gap: 156px;
    grid-template-columns: repeat(2, 1fr);
  }
}

.no-js .main-navigation__toggle {
  display: none;
}

.no-js .main-navigation__container {
  position: relative;
  display: block;
}

.main-navigation__container::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 7px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .main-navigation__container::after {
    display: none;
  }
}

.main-navigation--open .main-navigation__container {
  display: block;
}

.main-navigation--open .main-navigation__toggle {
  background-image: url("../img/icon-cross.svg");
}

@media (min-width: 768px) {
  .main-navigation__list {
    display: flex;
  }
}
@media (min-width: 768px) {
  .main-navigation__list:last-child {
    margin-left: auto;
  }
}

.main-navigation__item {
  padding: 17px 17px 13px;
  text-align: center;
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
@media (min-width: 768px) {
  .main-navigation__item {
    width: 153px;
    box-shadow: none;
  }
}
@media (min-width: 1200px) {
  .main-navigation__item {
    width: 240px;
  }
}

.main-navigation__item a {
  display: inline-block;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  font-family: "PT Sans", "Arial", sans-serif;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
}
@media (min-width: 768px) {
  .main-navigation__item a {
    font-size: 14px;
    line-height: 21px;
    color: #000000;
  }
}

